<template>
  <div class="teaching-book">
    <div class="teaching-nav">
      <!-- <ul>
        <li
          :class="teachClassListIndex == 999 ? 'active-li' : ''"
          @click="teachClassListBut()"
        >
          全部
        </li>
        <li>|</li>
        <li
          :class="teachClassListIndex == index ? 'active-li' : ''"
          v-for="(item, index) in teachClassList"
          :key="index"
          @click="teachClassListBut(item.id, index)"
        >
          {{ item.label }}
          <span v-if="index < teachClassList.length - 1">&nbsp;&nbsp;|</span>
        </li>
      </ul> -->
    </div>
    <div class="demo-book-resource">
      <div>
        <div class="left-category">
          <!-- 中图法分类 -->
          <div id="zdf" v-if="tabName == 'material' || tabName == 'learning'">
            <div
              class="title theme-title"
              :class="
                tabName == 'material' || tabName == 'learning'
                  ? 'active-title'
                  : ''
              "
              v-preventRepeat="800"
            >
              中图法分类
            </div>
            <ul class="drop-menu">
              <li
                class="all-class"
                :class="
                  tabName == 'material' || tabName == 'learning'
                    ? 'active-li2'
                    : ''
                "
                @click="getAllBook"
              >
                <a href="javascript:;"
                  ><span class="ellipsis-aRow">全部</span></a
                >
              </li>
              <li
                :class="currentIndex == index ? 'active-li2' : ''"
                v-for="(item, index) in lawList"
                :key="index"
                v-preventRepeat="800"
                @click="controlTree(index)"
              >
                <el-tooltip
                  class="el-tooltip_popper is-dark"
                  :content="item.label"
                  placement="top"
                  v-ak-tooltip-auto-show
                >
                  <a href="javascript:;"
                    ><span class="ellipsis-aRow">{{ item.label }}</span
                    ><img
                      :src="
                        require('@/assets/icon/' +
                          (currentIndex == index
                            ? 'down-arrow3.png'
                            : 'right-arrow1.png'))
                      "
                      alt=""
                  /></a>
                </el-tooltip>
                <ul class="menu-list">
                  <li
                    v-preventRepeat="800"
                    :class="childIndex == item1.id ? 'active-li3' : ''"
                    v-for="(item1, index1) in item.children"
                    :key="index1"
                    @click.stop="getBook(item1.id)"
                  >
                    <el-tooltip
                      :content="item1.label"
                      placement="top"
                      v-ak-tooltip-auto-show
                    >
                      <a href="javascript:;" class="ellipsis-aRow">{{
                        item1.label
                      }}</a>
                    </el-tooltip>
                  </li>
                </ul>
              </li>
            </ul>
          </div>
          <!-- 活页 -->
          <div id="hy" v-if="tabName == 'looseleafOrtextbook'">
            <div class="title theme-title active-title">全部</div>
            <ul class="drop-menu drop-menu2"></ul>
          </div>
          <!-- 自定义分类 -->
          <div id="zdy" v-if="tabName == 'reference' || tabName == 'other'">
            <div class="title theme-title active-title" @click="custom_type">
              自定义分类
            </div>
            <ul class="drop-menu drop-menu2">
              <li
                :class="
                  tabName == 'reference' || tabName == 'other'
                    ? 'active-li2'
                    : ''
                "
                @click="getAllBookC"
              >
                <a href="javascript:;"
                  ><span class="ellipsis-aRow">全部</span></a
                >
              </li>
              <li
                v-preventRepeat="800"
                :class="currentIndex == index ? 'active-li2' : ''"
                v-for="(item, index) in customList"
                :key="index"
                @click="noChildControlTree(item.id, index)"
              >
                <el-tooltip
                  :content="item.label"
                  placement="top"
                  v-ak-tooltip-auto-show
                >
                  <a href="javascript:;"
                    ><span class="ellipsis-aRow">{{ item.label }}</span></a
                  >
                </el-tooltip>
              </li>
            </ul>
          </div>
          <!-- 试卷 -->
          <div id="sj" v-if="tabName == 'paper'">
            <div
              class="title theme-title"
              :class="tabName == 'paper' ? 'active-title' : ''"
              v-preventRepeat="800"
            >
              学科分类
            </div>
            <ul class="drop-menu">
              <li
                class="all-class"
                :class="tabName == 'paper' ? 'active-li2' : ''"
                @click="getAllBook"
              >
                <a href="javascript:;"
                  ><span class="ellipsis-aRow">全部</span></a
                >
              </li>
              <li
                :class="currentIndex == index ? 'active-li2' : ''"
                v-for="(item, index) in subjectList"
                :key="index"
                v-preventRepeat="800"
                @click="controlTree(index)"
              >
                <el-tooltip
                  class="el-tooltip_popper is-dark"
                  :content="item.label"
                  placement="top"
                  v-ak-tooltip-auto-show
                >
                  <a href="javascript:;"
                    ><span class="ellipsis-aRow">{{ item.label }}</span
                    ><img
                      :src="
                        require('@/assets/icon/' +
                          (currentIndex == index
                            ? 'down-arrow3.png'
                            : 'right-arrow1.png'))
                      "
                      alt=""
                  /></a>
                </el-tooltip>
                <ul class="menu-list" ref="menus">
                  <li
                    v-preventRepeat="800"
                    :class="
                      childIndex == item1.id ? 'active-li4' : 'active-li5'
                    "
                    v-for="(item1, index1) in item.children"
                    :key="index1"
                    @click.stop="getsubjecttree(item1.id)"
                  >
                    <el-tooltip
                      :content="item1.label"
                      placement="top"
                      v-ak-tooltip-auto-show
                    >
                      <a href="javascript:;">
                        <span class="span5"> {{ item1.label }}</span>
                        <img
                          :src="
                            require('@/assets/icon/' +
                              (childIndex == item1.id
                                ? 'down-arrow3.png'
                                : 'right-arrow1.png'))
                          "
                          alt=""
                        />
                      </a>
                    </el-tooltip>
                    <ul class="menu-list1">
                      <li
                        v-preventRepeat="800"
                        :class="
                          subjectIdIndex == item2.id
                            ? 'active-li1'
                            : 'active-li2'
                        "
                        v-for="(item2, index2) in item1.children"
                        :key="index2"
                        @click.stop="getsubjectId(item2.id)"
                      >
                        <el-tooltip
                          :content="item2.label"
                          placement="top"
                          v-ak-tooltip-auto-show
                        >
                          <a href="javascript:;">
                            <span> {{ item2.label }}</span>
                          </a>
                        </el-tooltip>
                      </li>
                    </ul>
                  </li>
                </ul>
              </li>
            </ul>
          </div>
          <!-- 培训 -->
          <div id="px" v-if="tabName == 'train'">
            <div class="title theme-title active-title">四套卷</div>
            <ul class="drop-menu drop-menu2">
              <li
                v-preventRepeat="800"
                :class="currentIndex == index ? 'active-li2' : ''"
                v-for="(item, index) in trainingList"
                :key="index"
                @click="trainChild(item.id, index)"
              >
                <el-tooltip
                  :content="item.label"
                  placement="top"
                  v-ak-tooltip-auto-show
                >
                  <a href="javascript:;"
                    ><span class="ellipsis-aRow">{{ item.label }}</span></a
                  >
                </el-tooltip>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="right-book-resource">
        <div class="header-select-search">
          <div class="resource-type" v-if="tabName != 'train'">
            资源类型：
            <span
              v-preventRepeat="800"
              :class="resourceTypeindex == 999 ? 'active-type' : ''"
              @click="resourceType()"
              >全部</span
            >
            <div>
              <span
                v-preventRepeat="800"
                :class="resourceTypeindex == index ? 'active-type' : ''"
                v-for="(item, index) in CResourceList"
                :key="index"
                @click="resourceType(item.label, index)"
                >{{ item.label }}</span
              >
            </div>
            <!-- <span
              v-if="tabName == 'train'"
              v-preventRepeat="800"
              :class="resourceTypeindex == 1 ? 'active-type' : ''"
              @click="resourceType('课程', 1)"
              >课程</span
            > -->
          </div>
          <div class="search">
            <input
              type="text"
              v-model.trim="queryParams.goodsTitle"
              class="custom-input"
              @keyup.enter="handleQuery"
              placeholder="请输入"
            />
            <el-button
              type="primary"
              class="custom-button"
              icon="el-icon-search"
              @click="handleQuery"
              >搜索</el-button
            >
          </div>
        </div>
        <el-tabs
          class="intabs"
          v-model="activeName"
          v-preventRepeat="800"
          @tab-click="handleClick"
        >
          <el-tab-pane name="first">
            <span slot="label">最新 </span>
            <booklist-box :data-list="bookList"></booklist-box>
            <noData v-if="total == 0"></noData>
            <div class="pagination-box" v-show="total > 0">
              <el-pagination
                class="ou-pagination"
                background
                hide-on-single-page
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryParams.pageNum"
                :page-sizes="[10, 20, 30, 40, 50]"
                :page-size="queryParams.pageSize"
                layout="total, prev, pager, next, sizes, jumper, slot"
                :total="total"
              >
                <el-button @click="getList()" class="ou-page-go">GO</el-button>
              </el-pagination>
            </div>
          </el-tab-pane>
          <el-tab-pane name="second">
            <span slot="label">价格 </span>
            <booklist-box :data-list="bookList"></booklist-box>
            <noData v-if="total == 0"></noData>
            <div class="pagination-box" v-show="total > 0">
              <el-pagination
                class="ou-pagination"
                background
                hide-on-single-page
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryParams.pageNum"
                :page-sizes="[10, 20, 30, 40, 50]"
                :page-size="queryParams.pageSize"
                layout="total, prev, pager, next, sizes, jumper, slot"
                :total="total"
              >
                <el-button @click="getList()" class="ou-page-go">GO</el-button>
              </el-pagination>
            </div>
          </el-tab-pane>
          <el-tab-pane name="third">
            <span slot="label">销量 </span>
            <booklist-box :data-list="bookList"></booklist-box>
            <noData v-if="total == 0"></noData>
            <div class="pagination-box" v-show="total > 0">
              <el-pagination
                class="ou-pagination"
                background
                hide-on-single-page
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryParams.pageNum"
                :page-sizes="[10, 20, 30, 40, 50]"
                :page-size="queryParams.pageSize"
                layout="total, prev, pager, next, sizes, jumper, slot"
                :total="total"
              >
                <el-button @click="getList()" class="ou-page-go">GO</el-button>
              </el-pagination>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>
<script>
import { TeachingClassList, goodsList } from "@/api/bookstore/bookstore";
import booklistBox from "@/components/booklistBox.vue";
import { queryTreeNoAllSubjectSelect } from "@/api/answer/answer";
import { paperGetToken } from "@/api/index/index";
import noData from "@/components/noData.vue";
export default {
  data() {
    return {
      childIndex: 0, //第二级分类
      currentIndex: -1, //第一级分类
      subjectIdIndex: "", //三级
      teachClassListIndex: 999,
      resourceTypeindex: 999, //资源类型
      subjectList: [],
      catalogType: "law_type",
      activeName: "first",
      total: 0,
      total2: 0,
      total3: 0,
      customList: [],
      bookList: [],
      queryParams: {
        goodsTitle: "",
        sourceFile: "", //资源类型
        teachProperty: "", //教学属性
        classification: "", //中图法分类
        selfType: "", //自定义分类
        goodsType: "material", //商品类型
        specialMark: "",
        sortList: [
          //最新销量热门
          {
            fieldName: "create_time",
            type: "desc"
          }
        ],
        pageNum: 1,
        pageSize: 10
      },
      trainingList: [
        {
          label: "全部",
          id: ""
        },
        {
          label: "是",
          id: "dollors"
        },
        {
          label: "否",
          id: "nothing"
        }
      ],
      sortType: "desc"
    };
  },
  props: {
    teachClassList: Array,
    lawList: Array,
    CResourceList: Array,
    tabName: String
  },
  components: { booklistBox, noData },
  watch: {
    //监听
    $route(to, from) {
      //路由变化方式，当路由跳转到书城列表时，方法就会执行
      if (to.path == "/bookstore/index") {
        this.getList();
      }
    }
  },
  created() {
    this.getList();
    this.isCustomClass();
    this.SubjectSelect();
  },
  methods: {
    isCustomClass() {
      if (
        this.tabName == "other" ||
        this.tabName == "reference" ||
        this.tabName == "looseleafOrtextbook"
      ) {
        this.custom_type();
      }
    },
    getList() {
      //获取最新图书列表
      this.queryParams.goodsType = this.tabName;
      goodsList(this.queryParams).then(res => {
        if (res.code == 200) {
          this.bookList = res.rows;
          this.total = res.total;
        }
      });
    },

    // 搜索按钮操作
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    //点击左侧中图法分类一级菜单
    controlTree(index) {
      if (this.currentIndex != index) {
        this.currentIndex = index;
      } else {
        this.currentIndex = -1;
      }
    },
    //点击左侧中图法分类二级菜单
    getBook(id) {
      this.childIndex = id;
      this.queryParams.classification = id;
      this.getList();
    },
    //点击自定义分类
    noChildControlTree(id, index) {
      this.currentIndex = index;
      this.queryParams.selfType = id;
      this.getList();
    },
    //乐学
    trainChild(id, index) {
      this.currentIndex = index;
      this.queryParams.specialMark = id;
      this.getList();
    },
    //科学分类二级级
    getsubjecttree(id) {
      if (this.childIndex != id) {
        this.childIndex = id;
      } else {
        this.childIndex = "";
      }
    },
    //科学分类三级
    getsubjectId(id) {
      this.subjectIdIndex = id;
      this.queryParams.subjectId = id;
      this.getList();
    },
    //点击自定义分类按钮
    custom_type() {
      //查询自定义分类下拉树结构
      this.catalogType = "custom_type";
      TeachingClassList(this.catalogType).then(res => {
        if (res.code == 200) {
          this.customList = res.data;
        }
      });
    },
    //科学分类
    SubjectSelect() {
      //查询自定义分类下拉树结构
      paperGetToken().then(res => {
        let data = {
          token: res.data
        };
        queryTreeNoAllSubjectSelect(data).then(res => {
          if (res.code == 200) {
            this.subjectList = res.data;
          }
        });
      });
    },
    getAllBook() {
      this.queryParams.classification = "";
      this.getList();
    },
    //自定义分类
    getAllBookC() {
      this.queryParams.selfType = "";
      this.getList();
    },
    teachClassListBut(id, index) {
      if (id) {
        this.teachClassListIndex = index;
        this.queryParams.teachProperty = id;
      } else {
        this.teachClassListIndex = 999;
        this.queryParams.teachProperty = "";
      }
      this.getList();
    },
    //资源类型
    resourceType(value, index) {
      if (value) {
        this.resourceTypeindex = index;
        this.queryParams.sourceFile = value;
      } else {
        this.resourceTypeindex = 999;
        this.queryParams.sourceFile = "";
      }
      if (value != "train") {
        this.getList();
      }
    },
    handleClick(tab) {
      this.currentIndex = tab.index;
      if (tab.index == 1) {
        this.queryParams.sortList = [
          {
            fieldName: "real_price",
            type: "desc"
          }
        ];
        this.getList();
      } else if (tab.index == 2) {
        this.queryParams.sortList = [
          {
            fieldName: "sales_volume",
            type: "desc"
          }
        ];
        this.getList();
      } else if (tab.index == 0) {
        this.queryParams.sortList = [
          {
            fieldName: "create_time",
            type: "desc"
          }
        ];
        this.getList();
      }
    },
    handleSizeChange(val) {
      this.queryParams.pageSize = val;
      this.getList();
    },
    handleCurrentChange(val) {
      this.queryParams.pageNum = val;
      this.getList();
    }
  }
};
</script>
<style scoped lang="scss">
.teaching-book {
  height: auto;
  background: #fbfbfb;
  .teaching-nav {
    width: 1400px;
    height: 5px;
    background: #fffefd;
    // background: #fff;
    margin: 0 auto 16px;
    box-sizing: border-box;
    padding-left: 40px;
    ul {
      display: flex;
      li {
        cursor: pointer;
        font-size: 14px;
        font-family: PingFang SC;
        font-weight: 400;
        color: #666666;
        margin-right: 11px;
      }
      .active-li {
        color: #2e9adb;
      }
    }
  }
  .demo-book-resource {
    width: 1400px;
    min-height: 460px;
    margin: 0 auto;
    display: flex;
    .left-category {
      width: 304px;
      background: #fffefd;
      margin-right: 16px;
      box-sizing: border-box;
      // padding: 12px 28px 14px 26px;
      padding: 12px 5px 14px 26px;
      .title {
        font-size: 14px;
        cursor: pointer;
      }
      .title {
        margin-bottom: 20px;
      }
      .title:nth-of-type(2) {
        margin-top: 10px;
      }
      .active-title {
        color: #2e9adb;
      }
      .drop-menu {
        height: 500px;
        overflow: auto;
        padding: 0 5px;
        .all-class {
          margin-bottom: 30px;
        }
        li {
          line-height: 21px;
          margin-bottom: 10px;
          a {
            text-decoration: none;
            font-size: 14px;
            font-family: PingFang SC;
            font-weight: 400;
            color: #333333;
            display: flex;
            align-items: center;
            justify-content: space-between;
            span {
              width: 208px;
            }
            img {
              width: 7px;
              height: 12px;
            }
          }
        }
        .active-li2 {
          > a {
            color: #2e9adb;
          }
          img {
            width: 12px;
            height: 7px;
          }
          .menu-list {
            height: 100%;
            visibility: visible;
            transform: rotateX(0deg);
          }
        }

        .menu-list {
          height: 0;
          transform-origin: top;
          transform: rotateX(90deg);
          transition: transform 0.2s;
          visibility: hidden;
          li {
            margin: 0 8px 30px 15px;
            a {
              width: 210px;
              display: block;
              box-sizing: border-box;
              padding: 5px 0 5px 5px;
            }
          }
          li:first-child {
            margin-top: 20px;
          }
          .active-li3 {
            background: #f2f2f2;
            border-radius: 6px;
            a {
              color: #2e9adb;
            }
          }
          .active-li4 {
            a {
              color: #2e9adb;
              display: flex;
              flex-direction: row;
              .span5 {
                display: block;
                width: 180px;
              }
            }
            img {
              width: 14px;
              height: 7px;
            }
            .menu-list1 {
              height: 100%;
              visibility: visible;
              transform: rotateX(0deg);
            }
          }
          .active-li5 {
            a {
              color: #000;
              display: flex;
              flex-direction: row;
              .span5 {
                display: block;
                width: 180px;
              }
            }
            img {
              width: 7px;
              height: 12px;
            }
          }
          .menu-list1 {
            height: 0;
            // transform-origin: top;
            // transform: rotateX(90deg);
            // transition: transform 0.2s;
            visibility: hidden;
            li {
              margin: 0 8px 30px 15px;
              a {
                width: 210px;
                display: block;
                box-sizing: border-box;
                padding: 5px 0 5px 5px;
              }
            }
            li:first-child {
              margin-top: 20px;
            }
            .active-li1 {
              background: #f2f2f2;
              border-radius: 6px;
              a {
                color: #2e9adb;
              }
            }
            .active-li2 {
              a {
                color: #000;
              }
            }
          }
        }
      }
      .drop-menu::-webkit-scrollbar {
        width: 5px;
      }
      .drop-menu::-webkit-scrollbar-thumb {
        background-color: rgba(144, 147, 153, 0.3);
        border-radius: 4px;
      }
      .drop-menu::-webkit-scrollbar-thumb:hover {
        background-color: rgba(144, 147, 153, 0.5);
      }
      .drop-menu::-webkit-scrollbar-track {
        border-radius: 4px;
        background: #fff;
      }
      .drop-menu2 {
        li {
          margin-bottom: 30px;
        }
        li:last-child {
          margin-bottom: 0;
        }
      }
    }
    .right-book-resource {
      width: 1080px;
      min-height: 460px;
      background: #fffefd;
      box-sizing: border-box;
      padding-left: 20px;
      .header-select-search {
        margin-top: 4px;
        height: 35px;
        display: flex;
        align-items: center;
        position: relative;
        .resource-type {
          font-size: 14px;
          font-family: PingFang SC;
          font-weight: 400;
          color: #666666;
          display: flex;
          align-items: center;
          span {
            cursor: pointer;
            text-align: center;
            padding: 5px 10px;
            height: 22px;
            box-sizing: border-box;
            line-height: 12px;
          }
          .active-type {
            background: #2e9adb;
            border-radius: 11px;
            color: #ffffff;
          }
        }

        .search {
          height: 32px;
          position: absolute;
          right: 30px;
          top: 20px;
          z-index: 999;
        }
      }
    }
  }
}
.pagination-box {
  text-align: right;
  margin: 25px 0;
}
</style>
