<template>
  <div class="bookstore">
    <div class="bookstore-content">
      <el-tabs class="outtabs" v-model="activeName" @tab-click="handleClick">
        <el-tab-pane
          :name="item.dictValue"
          v-for="(item, index) in teachingList"
          :key="index"
        >
          <span slot="label">{{ item.dictLabel }}</span>
          <teaching-book-box
            v-if="activeIndex == index"
            :teach-class-list="teachClassList"
            :law-list="lawList"
            :c-resource-list="CResourceList"
            :tab-name="activeName"
          ></teaching-book-box>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>
<script>
import { teachingAttrList, TeachingClassList } from "@/api/bookstore/bookstore";
import teachingBookBox from "@/components/teachingBookBox";
export default {
  data() {
    return {
      dictType: "oc_teach_property",
      activeName: "material",
      catalogType: "law_type",
      activeIndex: 0,
      teachingList: [], //教学属性列表
      teachClassList: [], //教学属性下的分类
      lawList: [], //中图法分类列表
      CResourceList: [], //当前商品列表
      remarkArray: []
    };
  },
  components: { teachingBookBox },
  watch: {
    //监听
    $route(to, from) {
      //路由变化方式，当路由跳转到书城列表时，方法就会执行
      if (to.path == "/bookstore/index") {
        if (this.$route.query.activeIndex) {
          this.activeIndex = this.$route.query.activeIndex;
          this.activeName = this.$route.query.activeName;
        }
        this.getList();
      }
    }
  },
  created() {
    this.getList();
  },
  methods: {
    getList() {
      //获取教学属性列表
      teachingAttrList(this.dictType).then(res => {
        if (res.code == 200) {
          this.teachingList = [];
          res.data.map(item => {
            if (item.dictValue != "reference" && item.dictValue != "other") {
              this.teachingList.push(item);
            }
          });

          this.teachingList.forEach(item => {
            if (this.activeName == item.dictValue) {
              this.remarkArray = eval(item.remark);
            }
          });
        }
      });
      //查询某一项教学属性下的分类
      let catalogType = this.activeName + "_property";
      TeachingClassList(catalogType).then(res => {
        if (res.code == 200) {
          this.teachClassList = res.data;
        }
      });
      //查询中图法分类下拉树结构
      TeachingClassList(this.catalogType).then(res => {
        if (res.code == 200) {
          this.lawList = res.data;
        }
      });
      //根据教学属性获取右侧资源类型
      let dictType = "oc_resource_file";
      setTimeout(() => {
        teachingAttrList(dictType).then(res => {
          if (res.code == 200) {
            this.CResourceList = [];
            var resourceList = res.data;
            for (var i = 0; i < resourceList.length; i++) {
              var option = {
                value: resourceList[i].dictValue,
                label: resourceList[i].dictLabel
              };
              this.CResourceList.push(option);
            }
          }
        });
      }, 100);
    },
    handleClick(tab) {
      this.getList();
      if (tab.index != this.activeIndex) {
        this.activeIndex = tab.index;
      }
    }
  }
};
</script>
<style scoped lang="scss">
.bookstore {
  padding-bottom: 40px;
  // background: #FBFBFB;
  .bookstore-content {
    width: 1400px;
    margin: 0 auto;
    height: auto;
    background: #fffefd;
    box-sizing: border-box;
  }
}
</style>
